.list {
  display: flex;
  flex-wrap: wrap;
  gap: 8%;
  justify-content: center;

  @include desktop {
    gap: 4%;
  }
}

.list__item {
  padding: 3px;
  align-items: center;
  cursor: pointer;
}

.list__button {
  display: block;
  width: 18px;
  height: 14px;
  border-radius: 70%;
  border: none;
  background-color: rgba(255, 89, 89, 0.5);

  @include desktop {
    width: 42px;
    height: 18px;

    &--current-dot {
      background-color: rgba(255, 89, 89);
    }
  }
}
