@font-face {
  font-family: "Andika";
  font-style: normal;
  font-weight: 400;
  src: url("../Andika-Regular.woff2") format("woff2"), url("../Andika-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Andika";
  font-style: normal;
  font-weight: 700;
  src: url("../Andika-Bold.woff2") format("woff2"), url("../Andika-Bold.woff") format("woff");
  font-display: swap;
}
.container {
  min-height: 100vh;
  width: 100%;
  max-width: 370px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
@media screen and (min-width: 1250px) {
  .container {
    max-width: 1250px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.button {
  font-family: inherit;
  font-style: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
  display: block;
  text-decoration: none;
  text-align: center;
  padding: 15px 0;
}

.button--primary-color {
  margin-left: 20%;
  margin-right: -25px;
  background-color: rgb(252, 89, 89);
  min-width: 280px;
  height: 58px;
  border-radius: 10px 0 0 10px;
  color: rgb(255, 255, 255);
}
.button--primary-color:hover {
  box-shadow: 0 -5px 5px 0 rgba(0, 0, 0, 0.3);
}
.button--primary-color:active {
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.1);
}
@media screen and (min-width: 1250px) {
  .button--primary-color {
    margin: 0 auto;
    max-width: 450px;
    border-radius: 20px;
  }
}

.button--secondary-color {
  background-color: #ff5959;
  margin-right: 20%;
  margin-left: -25px;
  min-width: 280px;
  height: 58px;
  border-radius: 0 10px 10px 0;
  margin-top: 30px;
  color: #ffffff;
}
.button--secondary-color:hover {
  box-shadow: 0 -5px 5px 0 rgba(0, 0, 0, 0.3);
}
.button--secondary-color:active {
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.1);
}
@media screen and (min-width: 1250px) {
  .button--secondary-color {
    margin: 50px auto;
    max-width: 450px;
    border-radius: 20px;
  }
}

.button__lady {
  margin: 20px 0 0 -26px;
  padding: 16px 10px;
  min-width: 300px;
  background-color: rgb(255, 184, 200);
  color: rgb(0, 0, 0);
}
@media screen and (min-width: 1250px) {
  .button__lady {
    margin: 10px;
    margin-left: auto;
  }
}

.footer {
  background-color: rgb(255, 233, 184);
  padding: 20px 40px;
}
@media screen and (min-width: 1250px) {
  .footer {
    padding: 50px 80px;
