  }
}

.hero__item {
  margin-bottom: 30px;
  position: relative;
}
.hero__item--marker::before {
  content: "";
  position: absolute;
  color: #333333;
  width: 10px;
  height: 7px;
  background-color: rgb(143, 216, 248);
  border-radius: 50%;
  border: rgba(0, 0, 0, 0.5) solid 1px;
  left: -28px;
  top: 8px;
}
.hero__item--check::before {
  content: "";
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  width: 30px;
  height: 30px;
  background-image: url("../img/icon/marker.svg");
  left: -40px;
  top: -3px;
}
@media screen and (min-width: 1250px) {
  .hero__item--check::before {
    top: 0;
  }
}
.hero__item--heart::before {
  content: "";
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  background-image: url("../img/icon/heart.svg");
  left: -40px;
  top: -3px;
}
@media screen and (min-width: 1250px) {
  .hero__item--heart::before {
    left: 50%;
    transform: translateX(-50%);
    top: 60%;
  }
}
@media screen and (min-width: 1250px) {
  .hero__item--heart {
    display: block;
    border-radius: 20px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    max-width: 300px;
    min-height: 200px;
    padding: 40px;
    text-align: center;
  }
  .hero__item--arr-down::after {
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("../img/icon/arr-black2-price-1.svg");
    position: absolute;
    content: "";
    width: 150px;
    height: 110px;
    top: 96%;
  }
  .hero__item--arr-left::after {
    position: absolute;
    content: "";
    width: 150px;
    height: 110px;
    background-image: url("../img/icon/arr-black2-price.svg");
    background-position: center;
    background-repeat: no-repeat;
    top: 58%;
    left: -41%;
  }
  .hero__item--arr-right::after {
    position: absolute;
    content: "";
    width: 150px;
    height: 110px;
    background-image: url("../img/icon/arr-black2-price-2.svg");
    background-position: center;
    background-repeat: no-repeat;
    left: 95%;
  }
}

.hero__item--ordered::before {
  display: block;
  position: absolute;
  color: #333333;
  counter-increment: changes-list;
  content: counter(changes-list) ". ";
  font-size: inherit;
  line-height: inherit;
  left: -30px;
  font-weight: bold;
}

