.slider__img {
  display: block;
  margin: 0 auto;
}
.slider__img--hidden {
  display: none;
}
@media screen and (min-width: 1250px) {
  .slider__img {
    width: 320px;
    height: 404px;
  }
  .slider__img--hidden {
    display: block;
  }
}